*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: rgb(47, 47, 163);
  color: white;
  position: relative; /* Ensure it's positioned for z-index */
  z-index: 10; 
}
.logo{
  text-decoration: none;
  color: aliceblue;
  font-weight: 400;
  font-size: 25px;
}

nav a{
  margin: 0 2rem;
  color: white;
  text-decoration: none;
}

nav a:hover{
  color: aqua;
}

header .nav-btn{
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: aliceblue;
  visibility: hidden;
  opacity: 0;
  font-size: 2rem;
}

@media only screen and (max-width: 1024px){
  header .nav-btn{
    visibility: visible;
    opacity: 1;
  }

  header nav{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: rgb(47, 47, 163);
    transition: 1s;
    transform: translateY(-100vh);
  }

  header .responsive_nav{
    transform: none;
  }
  nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  nav a{
    font-size: 1.5rem;
  }
  
}